*{
  background-color: #21232A;
  font-family: 'Courier New', Courier, monospace;
  color: beige;
  
}
body{
  padding: 60px; ;
}
input{
  color: gray;
  font-size: 1.3rem;
  width: 100%;

}
.card{
  align-content: center;
  border: 1px solid gray;
  border-radius: 6px;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;
}
.card p{
  padding: 5px;
  text-align: center;
}
.card img{
  max-height: 170px;


}
.cardGift{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;

}